main {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background: rgb(235, 214, 196);
}
.list-area {
  width: 60vw;
  display: flex;
  justify-content: space-between;
}
.list-area input {
  width: calc(100% - 50px - 5px);
  height: 40px; 
  padding-left: 5px;
  border: 1px solid rgb(197, 170, 146);
}
.list-area input + button {
  width: 50px;
  height: 42px;
  border: none;
  background: rgb(76, 148, 107);
  font-size: 16px;
  font-weight: 500;
  color: #fff;
  margin-left: 5px;
  vertical-align: middle;
}
.todo-board {
  width: 60vw;
  margin-top: 60px;
}
h1 {
  font-size: 26px;
  font-weight: 600;
  margin-bottom: 20px;
  text-transform: uppercase;
  color: #000;
}
.todo-item {
  display: flex;
  justify-content: space-between;
  width: 100%; 
  border: 1px solid #d4c09b;
  background: #fff;
  margin-bottom: 5px;
  line-height: 26px;
  padding: 10px;
  box-sizing: border-box;
}
.todo-item button {
  background: saddlebrown;
  color: #fff;
  border: none;
  height: 26px;
  width: 50px;
  border-radius: 3px;
}